  .csv-file-input {
  
    color: transparent;
  
    border-radius: 13px;
  
    width:50%;
  
    margin: 0 auto;
  
    display: inline-block;
  
    margin-left: 10%;
  
    margin-bottom: 5px;
  
    padding: 2px;
  
    font-weight: 700;
  
    font-Size:'14px' 
  
  }
  
  .csv-file-input::-webkit-file-upload-button {
  
    visibility: hidden;
  
  }
  
  .csv-file-input::before {
  
    content: 'CHOISIR UN FICHIER';
  
    color: white;
  
    display: inline-block;
  
    background: #609BF4;
  
    border-radius: 13px;
  
    padding: 7px ;
  
    outline: none;
  
    white-space: nowrap;
  
    cursor: pointer;
  
    font-weight: 700;
  
  font-Size:'14px' 
  
  }
  
  .csv-file-input:hover::before {
  
    border-color: black;
  
  }
  
  .csv-file-input:active {
  
    outline: 0;
  
  }
  
  .csv-file-input:active::before {
  
    background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9); 
  
  }
  
  
  
  .csv-file-input-hidden {
  
    color: transparent;
  
